@import "vars";
@import "mixins";

.gallery {
  &__photos {
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
  }
  &__link {
    color: $primary-blue-color;
  }
  &__caption {
    margin-top: 16px;
    font-weight: bold;
  }
  &__heading {
    @include crownHeadings;
  }
}
